@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #ffffff;

}


.Calculator_main__mEdpR{
    width: 400px;
    margin: auto;
    height: 550px;
    border: 1px solid #04FF4A;
    stroke: #04FF4A;
    border-radius: 10px;
    margin-top: 50px;
    background-color: aliceblue;
    color: #85BC2D;
}

.Calculator_actions__3pZK1{
    display: flex;
    justify-content: space-between;
    width: 300px;
    margin: auto;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;

}

.Calculator_heading__1voKY{
    width: 100px;
    font-size: 18px;
    color: #85BC2D;
}

.Calculator_actions_btn__3lYKh{
    display: flex;
    grid-gap: 15px;
    gap: 15px;
    align-items: center;
    background-color: #D2F290;
    height: 40px;
    padding: 15px;
    border-radius: 10px;
    font-size: 18px;
   
    
