.Calculator_actions_input__QGivH{
    border-radius: 50%;
    box-shadow: 2px 2px 2px rgb(142, 139, 140) inset;
    background-color: #D2F290;
    width: 30px;
    text-align: center;
    color: black;
}
button{
    height: 25px;
    background-color: transparent;
    border: none;
    font-size: 18px;
    text-align: center;
    width: 25px;
    cursor: pointer;
}
.Calculator_input_box__1JwgE{
    width: 300px;
    margin: auto;
   
}

input[type="range"]::-webkit-slider-runnable-track {
    background: #85BC2D;
    border-radius: 10px;
    height: 20px;
    
  }



input[type="range"] {
    -webkit-appearance: none;
    margin-right: 15px;
    width: 300px;
    height: 7px;
    border-radius: 5px;
    background-size: 70% 100%;
    background-repeat: no-repeat;
    margin-top: 50px;
  }
  
  /* Input Thumb */
  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background: #ACDF46;
    border: 5px solid #D2F290; 
    margin-top: -10px;
    cursor: pointer;
  }
  
  .Calculator_input_text__1T3lY{
    display: flex;
    justify-content: space-between;
    width: 300px;
    margin-top: 10px;
  }
  span{
    font-size: 14px;
    color: #85BC2D;
  }
  
  .Calculator_insight_box__fg2lv{
    width: 350px;
    margin: auto;
    margin-top: 20px;
    border-radius: 10px;
    border: 1px solid #85BC2D;
    padding: 10px;
    padding-bottom: 0px;
  }
  .Calculator_data_box__yy2tI{
    display: flex;
    justify-content: space-between;
    
  }
  .Calculator_sub_heading__2Rcu3{
    color: #85BC2D;
    font-size: 16px;
    margin-top: 0px;
  }

 .Calculator_sub_heading__2Rcu3> p{
    font-size: 14px;
  }


